import React from "react";
import ReactDOM from "react-dom/client";
import "./index.scss";
import "./i18n"
import App from "./App";
import { BrowserRouter } from "react-router-dom";

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <BrowserRouter basename="/my">
        <App />
    </BrowserRouter>
  </React.StrictMode>
);

